import { defineComponent, ref } from 'vue';
import Page from '@/components/admin/page';
import { Input, Form, Button, message, Checkbox } from 'ant-design-vue'
import router from '@/router'
import { TeamOutlined } from '@ant-design/icons-vue'


export default defineComponent(() => {

    const location = ref('test-router');
    const name = ref('');
    const keep = ref(true)
    const addName = ref('');

    const addRoute = () => {
        if(location.value.trim() == '') return message.error({content:<text></text>})
        if(name.value.trim() == '') return message.error({content:<text></text>})
        if(addName.value) {
            router.removeRoute(addName.value)
        }
        addName.value = 'test-router-mock-'+Math.random();
        router.addRoute({
            path: location.value,
            meta: { title: name.value, icon: TeamOutlined, keepAlive: keep.value },
            name: addName.value,
            component: () => <Page title=''>
                <Input placeholder='' />
            </Page>
        })
    }

    return () => <Page title=''>
        <Form layout='horizontal' label-col={{span:5}} wrapper-col={{span:12}}>
            <Form.Item label=''>
                {/* vue3modelValue */}
                <Input v-model={[location.value,'value']} />
            </Form.Item>
            <Form.Item label=''>
                <Input v-model={[name.value,'value']} />
            </Form.Item>
            <Form.Item label=''>
                <Checkbox v-model={[keep.value,'checked']}>keep-alive</Checkbox>
            </Form.Item>
            <Form.Item wrapper-col={{span:12,offset:5}}>
                <Button onClick={addRoute}></Button>
                <div>:</div>
            </Form.Item>
        </Form>

    </Page>

})